.settings{

}
.desc{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
}
.setImage{
    margin-top: 15px;

}
.setImage p{
    font-weight: bold;
    margin-bottom: 20px;
}
.setImage input{
    display: none;
}
.setImage label{
    padding: 10px 15px;
    border: 1px solid var(--hover-color);
    background-color: var(--hover-color);
    color: white;
    cursor: pointer;
    transition: .3s all ease;
    font-weight: bold;
    font-size: 18px;
    border-radius: 3px;
}
.setImage label:hover{
    background: transparent;
    color:var(--main-txt-color)
}